import {
	api
} from '../../api/api.js'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			btn: "发送",
			btnActive:false,
			time:60,
			timer:null,
			randoms: '',
			nicheng: '',
			tell: '',
			password: '',
			passwords: '',
			yqm: '',
			yzm: '',
			tellyzm: ''
		}
	},
	onLoad() {
		this.random();
	},
	methods: {
		back() {
			uni.navigateBack();
		},
		random() {
			this.randoms = "";
			for (var i = 1; i <= 4; i++) {
				const num = Math.floor(Math.random() * 10);
				this.randoms += num;
			}
		},
		checkPhone(phone) {
			if (!(/^1[3456789]\d{9}$/.test(phone))) {
				uni.showToast({
					title: '手机号错误',
					icon: 'none',
					duration: 1000
				})
				return false;
			}
		},
		countDwon() {
			this.timer = setInterval(()=>{
				this.btnActive = true;
				this.btn = this.time +("s");
				if(this.time != 0) {
					clearInterval(this.timer);
					this.btn = "发送";
					this.btnActive = false;
					return;
				}
			},1000);
		},
		sendYzm() {
			if (this.tell === "") {
				uni.showToast({
					title: '手机号不为空',
					icon: 'none',
					duration: 1000
				})
				return;
			}
			this.checkPhone(this.tell);
			this.countDwon() ;
			uni.request({
				url:api.reg_code,
				data: {
					mobile:this.tell
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res);
				}
			})
		},
		creatAccount() {
			if (this.nicheng === "") {
				uni.showToast({
					title: '请输入昵称',
					icon: 'none',
					duration: 1000
				})
				return;
			} else if (this.tell === "") {
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					duration: 1000
				})
				return;
			} else if (this.password === '') {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 1000
				})
				return;
			} else if (this.passwords === '') {
				uni.showToast({
					title: '请确认密码',
					icon: 'none',
					duration: 1000
				})
				return;
			} else if (this.yqm === "") {
				uni.showToast({
					title: '请输入邀请码',
					icon: 'none',
					duration: 1000
				})
				return;
			} else if (this.yqm === '') {
				uni.showToast({
					title: '请输入邀请码',
					icon: 'none',
					duration: 1000
				})
				return;
			}else if (this.yzm === '') {
				uni.showToast({
					title: '请输入验证码',
					icon: 'none',
					duration: 1000
				})
				return;
			}else if (this.yzm != this.randoms) {
				uni.showToast({
					title: '验证码错误',
					icon: 'none',
					duration: 1000
				})
				return;
			} else if (this.tellyzm === '') {
				uni.showToast({
					title: '请输入手机验证码',
					icon: 'none',
					duration: 1000
				})
				return;
			}
			
			uni.request({
				url: api.reg,
				method: 'POST',
				data: {
					invite_code: this.yqm,
					nickname:this.nicheng,
					mobile:this.tell,
					yzm_code:this.tellyzm,
					PassWord:this.password,
					PassWord1:this.passwords
				},
				success: (res) => {
					console.log(typeof(this.tell))
					console.log(res.data);
				}
			});

		}
	}
}
